import React, { Component } from 'react';

class Score extends Component{

    render() {
        var value = parseInt(this.props.value)
        var text
        if(value > 1000) {
            text = "Yay!!! :) "
        }
        else {
            text = "Mehhh :( "
        }
        return (
            <div>
                <label>Score: {text + value}</label>      
            </div>
        )
    }
}

export default Score